@charset "UTF-8";
/*! common */
.clfix:after, .clfix:before {
  display: table;
  line-height: 0;
  content: ""; }

.clfix:after {
  clear: both; }

.hide-txt {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  font-size: 1px;
  line-height: 0;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  margin: -1px;
  padding: 0;
  border: 0; }

@media (max-width: 1024px) {
  a .hide-txt {
    font-size: 0; } }

a:before {
  content: '　';
  position: absolute;
  font-size: 0; }

[tabindex="-1"]:focus {
  outline: 0; }
